import React, { Component } from "react";
import { strings } from "../../initialState";
import { topBairros } from "../../bairros";
import InputRange from "react-input-range";
import StickyBox from "react-sticky-box";
import CurrencyInput from "react-currency-input";
import "react-input-range/lib/css/index.css";

// Font Awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
library.add(fab, fas);

const WAIT_INTERVAL = 600;

class BuscaFormulario extends Component {
  state = {
    finalidade: "Comprar",
    showBairros: false,
    tipos: [],
    bairros: [],
    area: { minArea: null, maxArea: null },
    preco: { minPreco: null, maxPreco: null },
    quartos: [],
    vagas: []
  };
  componentDidMount() {
    if (this.props.finalidade === "venda") {
      this.setState({ finalidade: "Comprar" });
    } else if (this.props.finalidade === "locacao") {
      this.setState({ finalidade: "Alugar" });
    }
  }
  componentDidUpdate(prevProps, prevState) {
    if (this.props.finalidade !== prevProps.finalidade) {
      if (this.props.finalidade === "venda") {
        this.setState({ finalidade: "Comprar" });
      } else if (this.props.finalidade === "locacao") {
        this.setState({ finalidade: "Alugar" });
      }
    }
  }
  _handleShowBairros = () => {
    const showBairros = !this.state.showBairros;
    this.setState({ showBairros });
  };
  _handleChangeFinalidade = finalidade => {
    this.setState({ finalidade: finalidade }, () => {
      this.props.onChangeFinalidade(finalidade);
    });
  };
  _handleChangeTipo = event => {
    const tipos = [...this.state.tipos];
    const indexOf = tipos.indexOf(event.target.value);
    if (indexOf >= 0) {
      tipos.splice(indexOf, 1);
    } else {
      tipos.push(event.target.value);
    }
    this.setState({ tipos });
    this.props.onChangeTipo(tipos);
  };
  _handleChangeBairro = event => {
    const bairros = [...this.state.bairros];
    const indexOf = bairros.indexOf(event.target.value);
    if (indexOf >= 0) {
      bairros.splice(indexOf, 1);
    } else {
      bairros.push(event.target.value);
    }
    this.setState({ bairros });
    this.props.onChangeBairro(bairros);
  };
  _handleChangeArea = event => {
    clearTimeout(this.timer);

    const triggerChangeArea = () => {
      const area = { ...this.state.area };
      const name = event.target.name;
      let value = event.target.value.slice(0, -2);
      if (value <= 0) {
        value = null;
      } else {
        value = parseInt(value);
      }
      area[name] = value;
      this.setState({ area });
      this.props.onChangeArea(area);
    };

    this.timer = setTimeout(triggerChangeArea, WAIT_INTERVAL);
  };
  _handleChangePreco = event => {
    clearTimeout(this.timer);

    const triggerChangePreco = () => {
      const preco = { ...this.state.preco };
      const name = event.target.name;
      let value = event.target.value
        .slice(2)
        .split(".")
        .join("");
      if (value <= 0) {
        value = null;
      } else {
        value = parseInt(value);
      }
      preco[name] = value;
      this.setState({ preco });
      this.props.onChangePreco(preco);
    };

    this.timer = setTimeout(triggerChangePreco, WAIT_INTERVAL);
  };
  _handleChangeQuartos = event => {
    const quartos = [...this.state.quartos];
    const indexOf = quartos.indexOf(event.target.value);
    if (indexOf >= 0) {
      quartos.splice(indexOf, 1);
    } else {
      quartos.push(event.target.value);
    }
    this.setState({ quartos });
    this.props.onChangeQuartos(quartos);
  };
  _handleChangeVagas = event => {
    const vagas = [...this.state.vagas];
    const indexOf = vagas.indexOf(event.target.value);
    if (indexOf >= 0) {
      vagas.splice(indexOf, 1);
    } else {
      vagas.push(event.target.value);
    }
    this.setState({ vagas });
    this.props.onChangeVagas(vagas);
  };
  _handleSubmit = event => {
    event.preventDefault();
    console.log("submit prevent");
  };
  _handleLimparFiltros = () => {
    let checkboxes = new Array();
    checkboxes = document.getElementsByTagName("input");

    for (let i = 0; i < checkboxes.length; i++) {
      if (checkboxes[i].type == "checkbox") {
        checkboxes[i].checked = false;
      }
    }
    this.setState({
      tipos: [],
      bairros: [],
      area: { minArea: null, maxArea: null },
      preco: { minPreco: null, maxPreco: null },
      quartos: [],
      vagas: []
    });
    this.props.onLimparFiltros();
  };
  render() {
    const { imovelProp, buscaImoveis } = strings;
    const { lang } = this.props;
    return (
      <StickyBox offsetTop={15} offsetBottom={15}>
        <form className="buscaForm" onSubmit={this._handleSubmit.bind(this)}>
          <div className="row">
            <div
              className={
                this.state.finalidade === "Comprar"
                  ? "col-6 buscaForm__finalidade active"
                  : "col-6 buscaForm__finalidade"
              }
              onClick={() => this._handleChangeFinalidade("Comprar")}
            >
              <span>{imovelProp[lang].comprar}</span>
            </div>
            <div
              className={
                this.state.finalidade === "Alugar"
                  ? "col-6 buscaForm__finalidade active"
                  : "col-6 buscaForm__finalidade"
              }
              onClick={() => this._handleChangeFinalidade("Alugar")}
            >
              <span>{imovelProp[lang].alugar}</span>
            </div>
          </div>
          <div className="buscaForm__tipo buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "home"]} />
              {imovelProp[lang].tipoDoImovel}
            </h3>
            <div className="buscaForm__box_options">
              {["Casa", "Apartamento", "Cobertura", "Comercial"].map(item => {
                return (
                  <div className="form-check" key={`finalidade${item}`}>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value={item}
                      id={item}
                      onClick={this._handleChangeTipo.bind(this)}
                    />
                    <span className="checkmark" />
                    <label className="form-check-label" htmlFor={item}>
                      {imovelProp[lang][item]}
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__bairro buscaForm__box">
            <FontAwesomeIcon
              icon={
                this.state.showBairros
                  ? ["fas", "chevron-up"]
                  : ["fas", "chevron-down"]
              }
              onClick={this._handleShowBairros}
            />
            <h3 onClick={this._handleShowBairros}>
              <FontAwesomeIcon icon={["fas", "map-marker-alt"]} />
              {imovelProp[lang].bairro}
            </h3>
            <div
              className={
                this.state.showBairros
                  ? "buscaForm__bairro_box buscaForm__box_options"
                  : "buscaForm__bairro_box buscaForm__box_options hidden"
              }
            >
              {topBairros.map(item => {
                return (
                  <div className="form-check" key={`bairro${item.name}`}>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value={item.name}
                      id={item.name}
                      onClick={this._handleChangeBairro.bind(this)}
                    />
                    <span className="checkmark" />
                    <label className="form-check-label" htmlFor={item.name}>
                      {item.name}
                    </label>
                  </div>
                );
              })}
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="checkbox"
                  value="Outros"
                  id="outros"
                  onClick={this._handleChangeBairro.bind(this)}
                />
                <span className="checkmark" />
                <label className="form-check-label outros" htmlFor="outros">
                  Outros{" "}
                  <small>
                    <FontAwesomeIcon icon={["fas", "question-circle"]} />
                  </small>
                </label>
              </div>
            </div>
          </div>
          <div className="buscaForm__area buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "expand"]} />
              {imovelProp[lang].area}
            </h3>
            <span className="minMaxLabel">{imovelProp[lang].areaMinima}</span>

            <CurrencyInput
              className="minArea"
              value={this.state.area.minArea}
              suffix="m²"
              decimalSeparator=""
              thousandSeparator=""
              precision="0"
              name="minArea"
              onChangeEvent={this._handleChangeArea.bind(this)}
            />
            <span className="minMaxConector" />
            <span className="minMaxLabel">{imovelProp[lang].areaMaxima}</span>

            <CurrencyInput
              className="maxArea"
              value={this.state.area.maxArea}
              suffix="m²"
              decimalSeparator=""
              thousandSeparator=""
              precision="0"
              name="maxArea"
              onChangeEvent={this._handleChangeArea.bind(this)}
            />
          </div>
          <div className="buscaForm__preco buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "dollar-sign"]} />
              {imovelProp[lang].preco}
            </h3>
            <span className="minMaxLabel">{imovelProp[lang].precoMinimo}</span>
            <CurrencyInput
              className="minPreco"
              value={this.state.preco.minPreco}
              prefix="R$"
              decimalSeparator=","
              thousandSeparator="."
              precision="0"
              name="minPreco"
              onChangeEvent={this._handleChangePreco.bind(this)}
            />
            <span className="minMaxConector" />
            <span className="minMaxLabel">{imovelProp[lang].precoMaximo}</span>
            <CurrencyInput
              className="maxPreco"
              value={this.state.preco.maxPreco}
              prefix="R$"
              decimalSeparator=","
              thousandSeparator="."
              precision="0"
              name="maxPreco"
              onChangeEvent={this._handleChangePreco.bind(this)}
            />
          </div>
          <div className="buscaForm__quartos buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "bed"]} />
              {imovelProp[lang].quartos}
            </h3>
            <div className="buscaForm__squares">
              {" "}
              {["1+", "2+", "3+", "4+", "5+"].map(item => {
                return (
                  <div className="form-check" key={`quarto${item}`}>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value={item}
                      id={`quarto${item}`}
                      onClick={this._handleChangeQuartos.bind(this)}
                    />
                    <label
                      className="form-check-label"
                      htmlFor={`quarto${item}`}
                    >
                      {item}
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__vagas buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "car"]} />
              {imovelProp[lang].vagas}
            </h3>
            <div className="buscaForm__squares">
              {["1+", "2+", "3+", "4+", "5+"].map(item => {
                return (
                  <div className="form-check" key={`vaga${item}`}>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value={item}
                      id={`vaga${item}`}
                      onClick={this._handleChangeVagas.bind(this)}
                    />
                    <label className="form-check-label" htmlFor={`vaga${item}`}>
                      {item}
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__codigo__empty">
            <input
              className="codigoDoImovel"
              placeholder={imovelProp[lang].referencia}
            />
            <span onClick={() => alert("click")}>
              <FontAwesomeIcon icon={["fas", "arrow-right"]} />
            </span>
            <button
              className="btn btn-outline-danger btn-sm"
              onClick={this._handleLimparFiltros}
            >
              <FontAwesomeIcon icon={["fas", "trash-alt"]} />
              {buscaImoveis[lang].limparFiltros}
            </button>
          </div>
        </form>
      </StickyBox>
    );
  }
}

export default BuscaFormulario;
